<template>
  <div class="edit-activity-appliaction">
    <el-new-card v-loading="isLoading">
      <template slot="header">
        <div class="flex-vertical-center">
          {{ $route.meta.name }}
          <span class="desc"><i>*</i> 为必填项</span>
          <act-appl-sts-icon v-if="isEditPage" :status="form.status" point></act-appl-sts-icon>
        </div>
      </template>

      <el-form
        ref="form"
        v-loading.fullscreen.lock="isSubmiting"
        element-loading-text="提交中..."
        label-width="180px"
        inline-message
        :model="form"
        :rules="rules"
        :hide-required-asterisk="false">
        <!-- 基本信息 -->
        <h5 class="sub-title">基本信息</h5>

        <el-form-item label="活动名称:" prop="name">
          <el-input v-model="form.name" placeholder="请输入活动名称"></el-input>
        </el-form-item>
        <el-form-item label="部门/企业名称:" prop="deptId">
          <el-select v-if="isAdmin" v-model="form.deptId" placeholder="请选择部门" @visible-change="getDeptList" :loading="deptLoading">
            <el-option v-for="item in deptOptions" :label="item.label" :value="item.value" :key="item.value"></el-option>
          </el-select>
          <el-select v-else v-model="form.deptId" placeholder="请选择部门">
            <el-option :label="accountInfo.deptName" :value="accountInfo.deptId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="担当:" prop="contact">
          <el-select v-if="isAdmin" v-model="form.contact" placeholder="请选择担当">
            <el-option label="担当1" value="担当1"></el-option>
          </el-select>
          <el-select v-else v-model="form.contact" placeholder="请选择部门">
            <el-option :label="accountInfo.name" :value="accountInfo.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务所属:" prop="busType">
          <el-select v-model="form.busType" placeholder="请选择业务所属">
            <el-option label="客户业务" :value="2"></el-option>
            <el-option label="DLR业务" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="活动模式:" prop="mode">
          <el-select v-model="form.mode" placeholder="请选择活动模式">
            <el-option label="线上" :value="2"></el-option>
            <el-option label="线下" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="活动类型:" prop="type">
          <el-select v-model="form.type" placeholder="请选择活动类型">
            <el-option label="常规" :value="2"></el-option>
            <el-option label="节点" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="平台:" prop="plat">
          <el-checkbox-group v-model="form.plat">
            <el-checkbox label="App">App</el-checkbox>
            <el-checkbox label="H5">H5</el-checkbox>
            <el-checkbox label="小程序">小程序</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="活动周期:" prop="cycle1" v-if="form.type === 2">
          <el-date-picker v-model="form.cycle1" type="date" placeholder="请选择活动周期"></el-date-picker>
        </el-form-item>
        <el-form-item label="活动周期:" prop="cycle2" v-if="form.type === 3">
          <el-new-date-picker v-model="form.cycle2" style="width: 400px;"></el-new-date-picker>
        </el-form-item>
        <el-form-item label="开启形式:" prop="open">
          <el-select v-model="form.open" placeholder="请选择开启形式">
            <el-option label="手动启动" :value="1"></el-option>
            <el-option label="自动启动（按照活动开始时间）" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="活动简介:" prop="intro">
          <el-input
            v-model="form.intro"
            type="textarea"
            show-word-limit
            :maxlength="300"
            :rows="3"
            :autosize="{ minRows: 2, maxRows: 6 }">
          </el-input>
        </el-form-item>

        <el-divider></el-divider>

        <!-- 积分池设置 -->
        <h5 class="sub-title">积分池设置</h5>
        <el-form-item label="预算费用（元）:" prop="budget">
          <el-input v-model="form.budget" placeholder="请输入预算（元）"></el-input>
        </el-form-item>
        <el-form-item label="预算额度:" prop="budgetAmount">
          <el-input v-model="form.budgetAmount" placeholder="请输入预算额度"></el-input>
        </el-form-item>
        <el-form-item label="预算额度警戒线（%）:" prop="budgetAmountLine">
          <el-input v-model.number="form.budgetAmountLine" placeholder="请输入预算额度警戒值（触及警戒线发送系统通知，建议在预算额度内70%）"></el-input>
        </el-form-item>
        <el-form-item label="备用金（元）:" prop="imprest">
          <el-input v-model="form.imprest" placeholder="请输入备用金（元）"></el-input>
        </el-form-item>
        <el-form-item label="备用额度:" prop="imprestAmount">
          <el-input v-model="form.imprestAmount" placeholder="请输入备用额度"></el-input>
        </el-form-item>
        <el-form-item label="备用额度警戒线（%）:" prop="imprestAmountLine">
          <el-input v-model.number="form.imprestAmountLine" placeholder="请输入备用额度警戒线（触及警戒线发送系统通知，建议在预算额度内70%）"></el-input>
        </el-form-item>

        <el-divider></el-divider>

        <!-- 业务规则 -->
        <h5 class="sub-title">业务规则</h5>

        <!-- 活动整体规则配置 -->
        <business-rules :rules="form.wholeRules" ref="wholeRules">
          <span slot="header">活动整体规则配置</span>
        </business-rules>

        <!-- 用户行为规则配置 - 发行 -->
        <div class="bus-rules">
          <div class="rule-name">用户行为规则配置 - 发行<el-button size="small" icon="el-icon-plus" @click="popup('behaviorIssueRules')">新建</el-button></div>
          <div class="sub-bus-rules" v-for="r in form.behaviorIssueRules" :key="r.id">
            <div class="rule-name">
              {{ r.name }}
              <span>
                <el-button size="small" plain type="warning" icon="el-icon-edit-outline" @click="popup('behaviorIssueRules', r)">编辑</el-button>
                <el-popconfirm
                  title="是否确认删除该用户行为？"
                  icon="el-icon-warning"
                  iconColor="#F56C6C"
                  confirmButtonType="danger"
                  cancelButtonText="否"
                  :style="{ marginLeft: '10px' }"
                  @onConfirm="removeSub('behaviorIssueRules', r)">
                  <el-button slot="reference" size="small" plain type="danger" icon="el-icon-remove-outline">删除</el-button>
                </el-popconfirm>
              </span>
            </div>
            <el-table
              :data="r.rules"
              stripe
              style="width: 100%">
                <el-table-column prop="rid" label="规则ID" align="center" width="120px"></el-table-column>
                <el-table-column prop="name" label="规则名称"></el-table-column>
                <el-table-column prop="desc" label="规则说明"></el-table-column>
              </el-table>
          </div>
        </div>

        <!-- 用户行为规则配置 - 核销 -->
        <div class="bus-rules">
          <div class="rule-name">用户行为规则配置 - 核销<el-button size="small" icon="el-icon-plus" @click="popup('behaviorVerifiRules')">新建</el-button></div>
          <div class="sub-bus-rules" v-for="r in form.behaviorVerifiRules" :key="r.id">
            <div class="rule-name">
              {{ r.name }}
              <span>
                <el-button size="small" plain type="warning" icon="el-icon-edit-outline" @click="popup('behaviorVerifiRules', r)">编辑</el-button>
                <el-popconfirm
                  title="是否确认删除该用户行为？"
                  icon="el-icon-warning"
                  iconColor="#F56C6C"
                  confirmButtonType="danger"
                  cancelButtonText="按错了"
                  :style="{ marginLeft: '10px' }"
                  @onConfirm="removeSub('behaviorVerifiRules', r)">
                  <el-button slot="reference" size="small" plain type="danger" icon="el-icon-remove-outline">删除</el-button>
                </el-popconfirm>
              </span>
            </div>
            <el-table
              :data="r.rules"
              stripe
              style="width: 100%">
                <el-table-column prop="rid" label="规则ID" align="center" width="120px"></el-table-column>
                <el-table-column prop="name" label="规则名称"></el-table-column>
                <el-table-column prop="desc" label="规则说明"></el-table-column>
              </el-table>
          </div>
        </div>

        <el-divider></el-divider>

        <h5 class="sub-title">附件上传</h5>
        <attachment :list.sync="form.attachment"></attachment>

        <el-divider></el-divider>

        <el-form-item>
          <el-button @click="onCancel" style="width: 120px">取 消</el-button>
          <el-button type="primary" @click="onVerily" style="width: 120px">{{ isEditPage ? '保 存' : '提 交' }}</el-button>
        </el-form-item>
      </el-form>
    </el-new-card>

    <!-- 新建用户规则弹窗 -->
    <el-dialog
      :title="(behaviorRuleForm.name ? '编辑' : '新建') + '用户行为规则 - ' + (popupType === 'behaviorIssueRules' ? '发行' : '核销')"
      width="60%"
      :visible.sync="popupSign"
      destroy-on-close
      :before-close="onCancelPopup"
      >

      <business-rules :rules="behaviorRuleForm.rules" ref="behaviorRules">
        <div class="behavior-rule-name" slot="header">
          <label><i>*</i>用户行为名称：</label>
          <el-input
            v-model="behaviorRuleForm.name"
            :class="{'is_error': behaviorNameErrMsg !== ''}"
            size="small"
            placeholder="请输入用户行为名称"
            @input="onInputRuleName"></el-input>
          <span class="behavior-name-err-msg">{{ behaviorNameErrMsg }}</span>
        </div>
      </business-rules>

      <el-divider></el-divider>

      <div class="btns-box">
        <el-button @click="onCancelPopup">取 消</el-button>
        <el-button type="primary" @click="popupSave" :loading="behaviorSaving">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import Attachment from '@/components/Attachment'
import ActApplStsIcon from '@/components/status/ActApplStsIcon'
import ElNewDatePicker from '@/components/ElNewDatePicker'
import BusinessRules from './BusinessRules'
import { mapGetters } from 'vuex'

export default {
  name: 'edit-account-application',
  components: {
    ElNewCard,
    Attachment,
    ActApplStsIcon,
    ElNewDatePicker,
    BusinessRules
  },
  data () {
    return {
      isEditPage: false,
      deptOptions: [], // GTMC部门选项
      form: { // 表单数据
        name: '',
        deptId: '',
        deptName: '',
        contact: '',
        busType: '',
        mode: '',
        type: '',
        plat: ['App'],
        cycle1: '',
        cycle2: [],
        open: '',
        intro: '',

        budget: '',
        budgetAmount: '',
        budgetAmountLine: '',
        imprest: '',
        imprestAmount: '',
        imprestAmountLine: '',

        attachment: [],
        wholeRules: [],
        behaviorIssueRules: [],
        behaviorVerifiRules: []
      },
      rules: { // 表单验证规则
        name: [{ required: true, max: 50, message: '请填写活动名称，长度不能超过50个汉字', trigger: 'blur' }],
        deptId: [{ required: true, message: '请选择部门/企业', trigger: 'change' }],
        contact: [{ required: true, message: '请选择担当', trigger: 'change' }],
        busType: [{ required: true, message: '请选择业务归属', trigger: 'change' }],
        mode: [{ required: true, message: '请选择活动模式', trigger: 'change' }],
        type: [{ required: true, message: '请选择活动类型', trigger: 'change' }],
        plat: [{ required: true, type: 'array', message: '请选择平台', trigger: 'change' }],
        cycle1: [{ required: true, message: '请选择活动周期', trigger: 'change' }],
        cycle2: [{ required: true, type: 'array', message: '请选择活动周期', trigger: 'change' }],
        open: [{ required: true, message: '请选择开启形式', trigger: 'change' }],
        budget: [{ required: true, pattern: /^\d{1,15}$/, message: '请填写整数数字，长度不能超出15个数字', trigger: 'blur' }],
        budgetAmount: [{ required: true, pattern: /^\d{1,15}$/, message: '请填写整数数字，长度不能超出15个数字', trigger: 'blur' }],
        budgetAmountLine: [{ required: true, type: 'number', min: 0, max: 100, message: '请填写0~100的整数数字', trigger: 'blur' }],
        imprest: [{ required: true, pattern: /^\d{1,15}$/, message: '请填写整数数字，长度不能超出15个数字', trigger: 'blur' }],
        imprestAmount: [{ required: true, pattern: /^\d{1,15}$/, message: '请填写整数数字，长度不能超出15个数字', trigger: 'blur' }],
        imprestAmountLine: [{ required: true, type: 'number', min: 0, max: 100, message: '请填写0~100的整数数字', trigger: 'blur' }]
      },
      popupSign: false, // 新建用户行为规则弹窗控制
      popupType: '', // 新建用户行为规则弹窗类型
      behaviorRuleForm: {}, // 用户行为新建数据
      behaviorNameErrMsg: '', // 用户行为名称错误提示语
      behaviorNameTimer: null,
      behaviorSaving: false,
      deptLoading: false,
      isSubmiting: false,
      isLoading: false
    }
  },
  computed: {
    ...mapGetters({
      accountInfo: 'getAccountInfo'
    }),

    isAdmin () {
      return this.accountInfo.isAdmin
    }
  },
  created () {
    // 根据是否有账户申请id，判断是创建还是编辑
    if (this.$route.query.id) {
      // 回显信息
      this.isEditPage = true
      this.getApplInfoById()
    }
  },
  methods: {
    // 获取可选的GTMC业务部门
    getDeptList (value) {
      if (value && this.deptOptions.length <= 0) {
        this.deptLoading = true
        this.$http.serviceGetDeptList()
          .then(res => {
            this.deptOptions = res.map(r => ({
              value: r.id,
              label: r.name
            }))
          })
          .catch(() => {
            this.deptOptions = []
          })
          .finally(() => {
            this.deptLoading = false
          })
      }
    },

    // 编辑时获取该id的申请信息
    getApplInfoById (id) {
      this.isLoading = true
      this.$http.serviceGetActApplDetail({ id })
        .then(res => {
          this.form = Object.assign({}, res.info, res.configAmountInfo)
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    // 提交前验证
    onVerily () {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$confirm(`是否${this.isEditPage ? '保存修改' : '确认提交'}？`)
            .then(() => {
              this.onSubmit()
            })
        } else {
          return false
        }
      })
    },

    // 提交申请
    onSubmit () {
      this.isSubmiting = true
      this.$http.serviceCreateAct(this.form)
        .then(res => {
          this.$message.success('提交成功')
          this.$router.replace({
            path: '/activityMgmt/actApplDetail',
            query: { id: res.id }
          })
        })
        .catch(error => {
          if (!error.code && error.name) Promise.reject(error)
          else this.$alert(error.message, '提交失败', { type: 'error' })
        })
        .finally(() => {
          this.isSubmiting = false
        })
    },

    // 取消申请
    onCancel () {
      this.$confirm(`是否取消${this.isEditPage ? '编辑' : '申请'}？`, '取消确认', { type: 'error' })
        .then(() => {
          this.$router.go(-1)
        })
    },

    // 初始化新建用户行为规则
    popup (type, data = {}) {
      this.behaviorRuleForm = Object.assign({}, {
        name: '',
        rules: []
      }, data)
      this.popupSign = true
      this.popupType = type
    },

    // 关闭新建用户行为弹窗
    closePopup () {
      this.popupSign = false
      this.behaviorRuleForm = {}
      this.behaviorNameErrMsg = ''
    },

    // 保存新建用户行为
    popupSave () {
      this.behaviorSaving = true
      this.checkRuleName(this.behaviorRuleForm.name)
        .then(() => {
          if (this.$refs.behaviorRules.validRules()) {
            this.$confirm('是否确认保存？').then(() => {
              this.form[this.popupType].push(this.behaviorRuleForm)
              this.closePopup()
            })
          }
        })
        .catch(() => {})
        .finally(() => {
          this.behaviorSaving = false
        })
    },

    // 取消新建用户行为
    onCancelPopup () {
      this.$confirm('是否取消' + (this.behaviorRuleForm.name ? '编辑' : '新建') + '用户行为规则？', { type: 'warning' })
        .then(this.closePopup)
    },

    onInputRuleName (val) {
      this.checkRuleName(val).catch(() => {})
    },

    // 检查名称输入
    checkRuleName (val) {
      return new Promise((resolve, reject) => {
        clearTimeout(this.behaviorNameTimer)
        this.behaviorNameTimer = setTimeout(() => {
          if (val.trim() === '' || val.length > 20) {
            this.behaviorNameErrMsg = '请填写用户行为名称，长度不能超出20个汉字'
            reject(new Error())
            return
          }

          this.behaviorNameErrMsg = ''

          this.isSameName(val)
            .then(res => {
              if (res) {
                this.behaviorNameErrMsg = '用户行为名称不可重复，请重新输入'
                reject(new Error())
              } else {
                resolve()
              }
            })
        }, 200)
      })
    },

    // 异步检查规则名称是否存在
    isSameName (val) {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          const res = Boolean(Math.floor(Math.random() * 2))
          resolve(res)
        }, 500)
      })
    },

    // 删除行为规则
    removeSub (type, data) {
      const idx = this.form[type].findIndex(r => r.name === data.name)
      this.form[type].splice(idx, 1)
    }
  }
}
</script>

<style lang="scss" scoped>
.edit-activity-appliaction {
  .desc {
    margin-left: 10px;
    font-size: 12px;
    font-weight: normal;
    color: #909399;
    i {
      color: #F56C6C;
    }
  }

  .el-form {
    .el-select, .el-input, .el-textarea {
      width: 400px;
    }
  }

  .flex-vertical-center {
    display: flex;
    align-items: center;
  }

  .btns-box {
    text-align: center;
    .el-button {
      width: 120px;
    }
  }

  .bus-rules {
    position: relative;
    margin-bottom: 40px;

    .rule-name {
      display: flex;
      justify-content: space-between;
      margin-bottom: 20px;
      font-size: 13px;
      font-weight: bold;
    }

    .el-table {
      font-size: 13px;

      ::v-deep th {
        background-color: #fafafa;
      }
    }

    .sub-bus-rules {
      margin-bottom: 20px;
    }
  }

  .behavior-rule-name {
    display: inline-flex;
    align-items: center;
    width: 100%;
    font-size: 13px;

    label {
      display: inline-block;
      width: 100px;
      i { color: #f56c6c; }
    }
    .el-input {
      width: 200px;
    }
    .behavior-name-err-msg {
      margin-left: 10px;
      font-size: 12px;
      font-weight: normal;
      color: #f56c6c;
    }

    ::v-deep .el-input.is_error .el-input__inner {
      border-color: #f56c6c;
    }
  }
}
</style>
