<template>
  <el-date-picker
    v-model="val"
    :type="type"
    range-separator="~"
    start-placeholder="开始日期"
    end-placeholder="结束日期"
    :editable="false"
    align="right"
    :popper-class="popperClass"
    :default-time="['00:00:00', '23:59:59']"
    :picker-options="pickerOptions"
    :style="{ width }"
    ref="datePicker">
  </el-date-picker>
</template>

<script>
import moment from 'moment'

export default {
  name: 'el-new-date-picker',
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: [Date, Array],
    type: {
      type: String,
      default: 'daterange'
    },
    popperClass: String,
    width: {
      type: String,
      default: '240px'
    }
  },
  data () {
    return {
      val: null,
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() > moment().startOf('day').toDate().getTime() - 1000
        }
      }
    }
  },
  watch: {
    value: {
      handler: function (val) {
        this.val = val
      },
      immediate: true
    },
    val: function (v) {
      this.$emit('change', v)
    }
  },
  methods: {
    focus () {
      this.$refs.datePicker.focus()
    }
  }
}
</script>

